.PicSlider {
  display: grid;
  grid-template:
    "thumbs thumbs thumbs" min-content
    "lbutton mainpic rbutton" min-content
    / 1fr 14fr 1fr;
  margin: auto;
  width: 80%;
}
.PicSlider-main-img {
  background-color: transparent;
  display: flex;
  align-content: center;
  grid-area: mainpic;
  justify-content: center;
  justify-items: center;
  text-align: center;
  width: 100%;
}

.PicSlider-btn {
  background-color: transparent;
  border: none;
  color:darkgray !important;
  justify-self: center;
  align-self: center;
}
.PicSlider-btn.btn-left {
  grid-area: lbutton;
}
.PicSlider-btn.btn-right {
  grid-area: rbutton;
}
.PicSlider-swipe-area {
  width: 100%;
}

@media only screen and (max-width: 768px) {
  .PicSlider {
    grid-template:
      "thumbs" min-content
      "mainpic" min-content;
    margin-top: 20px;
    width: 100%;
  }
  .PicSlider-main-img {
    margin-top: 10px;
  }
  .PicSlider-btn {
    display: none;
  }
}